.footer {
    height: 30vh;
    background-color: #ebffe4;
}

.container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    border: 1px solid;
}

.items {
    border: 1px solid;
    display: flex;
    width: 550px;
    height: 70px;
    justify-content: space-between;
}

@media screen and (max-width: 690px) {
    .items {
       width: 250px;
        height: 35px;
    }
}

